<template>
  <v-card flat>

    <v-tabs
        class="justify-center d-flex"
        v-model="tabBean"
    >
      <v-tab v-for="title in tabTitles" :key="title"
             @click="getCurrentItem($event,title)">
        {{ title }}
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tabBean">
      <v-tab-item v-for="title in tabTitles" :key="title">
        <slot :currentItem="currentItem"></slot>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>
<script>

export default {
  name: "BlogTab",
  props: {
    tabTitles: {
      type: Array,
      default: () => {
        return ["tab1", "tab2", "tab3", "tab4"]
      }
    },

  },
  data: () => ({
    tabBean: null,
    all: "all",
    currentItem: "",
  }),
  methods: {
    getCurrentItem(env, str) {
      this.currentItem = str
      console.log(str)
    }
  },
  mounted() {
    this.currentItem = this.tabTitles[0]
  }
}
</script>